import React from "react"
import Typography from "@mui/joy/Typography"
import HomeRoundedIcon from '@mui/icons-material/HomeRounded'
import Stack from "@mui/joy/Stack"
import Divider from "@mui/joy/Divider"

export const pageProperties = {
    title: "",
    icon: <HomeRoundedIcon />,
    selectedIcon: <HomeRoundedIcon color="primary" />
}

export default function LandingPage({ isDesktopLayout }: { isDesktopLayout: boolean }) {
    return (
        <Stack width="100%" justifyContent="start" direction="row" useFlexGap spacing={4} pt={2} pb={4} maxHeight="100%" >
            <Stack width={isDesktopLayout ? "50%" : "100%"} spacing={1} px={isDesktopLayout ? 21 : 1} overflow="auto">
                <Typography level="h1">   Unit404</Typography>
                <Divider />
                <Typography level="body-md">Unit404       Unit4.</Typography>
                <Typography level="title-md">:     C#,        Java.</Typography>
                <Typography level="body-md">  -<Typography component="a" href="mailto: smorodinskyomer@gmail.com" variant="plain" color="primary"> ()</Typography></Typography>
                <Typography level="body-md">    <Typography component="a" href="https://github.com/omer-sm/unit404" variant="plain" color="primary"></Typography></Typography>
            </Stack>
        </Stack>
    )
}